export interface BaseOptions {
  /**
   * Prevents hooks from running for the operation. Can only be used in the [backend code of a Wix site](https://dev.wix.com/docs/develop-websites/articles/coding-with-velo/backend-code/about-the-site-backend).
   *
   * Default: `false`.
   */
  suppressHooks?: boolean

  /**
   * When `true`, operations include draft items. Read operations include draft items in their response, and write operations modify draft items.
   *
   * Default: `false`.
   */
  showDrafts?: boolean
}

export interface WithAppOptions {
  /**
   * Options for [querying Wix app collections](https://dev.wix.com/docs/develop-websites/articles/wix-apps/wix-app-collections/querying-wix-app-collections).
   */
  appOptions?: Record<string, any>
}

export interface WixDataOptions extends BaseOptions, WithAppOptions {}

export interface WixDataReadOptions extends WixDataOptions, WithAppOptions {
  /**
   * Language to translate result text into, in [IETF BCP 47 language tag](https://en.wikipedia.org/wiki/IETF_language_tag) format.
   * If provided, the result text is returned in the specified language. If not provided, the result text is not translated.
   *
   * > **Note:** Translation for the specified language must be enabled for the collection in [Wix Multilingual](https://www.wix.com/app-market/wix-multilingual).
   *
   */
  language?: string

  /**
   * When `true`, reads data from the primary database instance. This decreases performance but ensures data retrieved is
   * up-to-date even immediately after an update. Learn more about [Wix Data and Eventual Consistency](https://dev.wix.com/docs/sdk/backend-modules/data/eventual-consistency).
   */
  consistentRead?: boolean
}

export interface WixDataReadWithProjectionOptions
  extends WixDataReadOptions,
    ProjectionOptions {}

export interface WixDataUpdateOptions extends WixDataOptions {
  /**
   * If true, referenced items are included.
   * @internal
   */
  includeReferences?: boolean
}

export interface WixDataPatchOptions extends BaseOptions {}

export interface WixDataAggregateOptions extends WixDataQueryOptions {}

export interface WixDataQueryOptions extends WixDataReadOptions {
  /**
   * When `true`, the query result include a `totalCount` and `totalPages` properties containing the totals of items
   * matching query. Requesting total count slows down the query.
   *
   * Default: `false`
   */
  returnTotalCount?: boolean
}

export interface WixDataQueryReferencedOptions
  extends WixDataQueryOptions,
    ProjectionOptions {
  /**
   * Order of the returned referenced items. Sorted by the date each item was referenced.
   *
   * Default: `asc`
   */
  order?: 'asc' | 'desc'

  /**
   * Number of items to load.
   *
   * Default: `50`
   */
  skip?: number

  /**
   * Number of items to skip in the current sort order.
   *
   * Default: `0`
   */
  limit?: number
}

interface ProjectionOptions {
  /**
   * Lists of fields to return in a result.
   */
  fields?: string[]
}

export interface WixDataItem {
  /**
   * Data item ID.
   */
  _id: string

  /**
   * Date and time the item was added to the collection.
   * @readonly
   */
  _createdDate?: Date

  /**
   * Date and time the item was last modified. When the item is first inserted, `_createdDate` and `_updatedDate` have
   * the same value.
   * @readonly
   */
  _updatedDate?: Date

  /**
   * ID of the user who created the item. Can be modified with site owner permissions.
   */
  _owner?: string

  /**
   * Data item contents.
   */
  [key: string]: any
}

export type WixDataItemOrId = WixDataItem | string
export type Comparable = string | number | Date

export interface WixDataReference {
  relationshipName: string
  left: WixDataItemOrId
  right: WixDataItemOrId
}

export interface Provider<T> {
  get(): T | Promise<T>
}

export interface WixDataBulkError extends Error {
  /**
   * Description of the error.
   */
  message: string

  /**
   * Error code.
   */
  code: string

  /**
   * Index of the item within the request array. Allows for correlation between request and response items.
   */
  originalIndex: number

  /**
   * Failed item (or ID in case of `bulkRemove()`).
   */
  item: WixDataItem | string
}

export interface WixDataBulkResult {
  /**
   * Number of inserted items.
   */
  inserted: number

  /**
   * Number of updated items.
   */
  updated: number

  /**
   * Number of removed items.
   */
  removed: number

  /**
   * Number of skipped items.
   */
  skipped: number

  /**
   * List of errors.
   */
  errors: WixDataBulkError[]

  /**
   * List of IDs of inserted items.
   */
  insertedItemIds: string[]

  /**
   * List of IDs of updated items.
   */
  updatedItemIds: string[]

  /**
   * List of IDs of removed items.
   */
  removedItemIds: string[]
}
